nav {

    @include flex-align;
    background-color: #131927;
    width: 100vw;
    height: 66px;
    padding: 0 30px;
    justify-content: space-between;
    
    ul {
       
        @include flex-align;

        li:first-child {
            display: flex;
            flex-direction: column;
            gap: 4px;
            margin-top: 5px;
            cursor: pointer;

            span:first-child {
                height: 2.5px;
            }

            span {
                background-color: rgba(256, 256, 256, 0.7);
                height: 1px;
                width: 19px;

            }

            &:hover {
                span {
                    background-color: rgba(256, 256, 256, 1);

                }
            }
        }

        li {
            a {
                color: rgba(256, 256, 256, 0.7);
                padding: 20px 15px;
                font-size: 16px;
            }
            &:hover{
                a{
                color: rgba(256, 256, 256, 1);

                }
            }
        }
        .search{
            display: flex;
            color: white;
            // border-bottom: 1px solid white;
            // width: 10vw;
            input{
                background-color: transparent;
                outline: none;
                color: white;
                border: none;
                width: 15vw;
                border-bottom: 1px solid rgba(256, 256, 256, .5) ;
            }
            
        }
        .subscribe{
            a{
                font-size: 0.8rem;
                font-weight: bold;
                padding: 3px 15px ;
                background-color: #1f80e0;
                border-radius: 5px;
                color: white;
            }
        }
    }


}