.latest {
    color: white;
    margin: 60px;
    padding-bottom: 100px;

    .title {
        margin-bottom: 20px;
    }

    .slick-list {
        overflow: visible !important;

    }

    .slick-prev {
        &::before {
            content: "\f053" !important;
        }

        left: -70px;
        background:linear-gradient(to right, #030b17, transparent);

        &:hover,
        &:focus {
            background:#030b17 !important;
            // background: linear-gradient(to right, #030b17, transparent) !important;
            background: opacity(1) !important;

        }
    }

    .slick-next {
        &::before {
            content: "\f054" !important;
        }

        right: -70px;
        background:linear-gradient(to left, #030b17, transparent);

        &:hover{
            // background: linear-gradient(to left, #030b17, transparent) !important ;
            background: currentColor !important;
            // color: red;
            // font-size: 100px;
            background-color: orange !important;
        }
    }

    .slick-prev,
    .slick-next {

        &::before {
            font-family: FontAwesome;
            font-size: 28px !important;
            display: none;

        }

        height: 110%;
        width: 80px;
        z-index: 99;
        opacity: 1 !important;

        &.slick-disabled:before {
            opacity: 0 !important;
        }

        &:hover,
        &:focus {
            background: local !important;
            ;

        }
    }

    .slider {

        &:hover>.slick-prev:before,
        &:hover>.slick-next:before {
            display: block;
        }
    }

}

@media screen and (max-width:576px) {

    .slick-arrow {
        display: none !important;
    }
    .latest{
    margin: 60px 5px 0 5px;

    }

    }