.vcard {
    height: 205px;
    width: 155px;
    border-radius: 5px;
    border-radius: 5px;
    background-position: center;
    background-size: contain;
    transition: all 0.4s;
    position: relative;
    z-index: 997;
    transform: scale(1.08);
    cursor: pointer;
    &::after {
        content: "";
        height: 140px;
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0%;
        background: linear-gradient(to top, #131927 55%, transparent);
        display: none;
        border-radius: 0 0 5px 5px;
    }

    &:hover {
        transform: scale(1.4);
        z-index: 998;

        .wrap {
            display: block;
        }

        &:after {
            display: block;
        }
    }

    .wrap {
        margin-bottom: 4px;
        opacity: 0.8;
        display: none;
        position: absolute;
        bottom: 0;
        z-index: 99;
        left: 5px;
        padding: 2px;
        h1 {
            font-size: 10px;
        }

        h2 {
            font-size: 9px;
        }

        p {
            font-size: 8px;

        }

        button {
            all: unset;
            font-size: 9px;
            padding: 2px;
            width: 141px;
            cursor: pointer;
            border-radius: 2px;
            text-transform: uppercase;
            &:hover {
                background-color: #334366;
            }

            i {
                color: white;
                padding-right: 15px;

            }
        }
    }
}


@media screen and (max-width:640px) {

.vcard {
    transform: scale(0.8);
    &:hover{
        transform: scale(0.8);
    }
}
}
@media screen and (max-width:576px) {

    .vcard {
        transform: scale(0.7);
        &:hover{
            transform: scale(0.7);
        }
    }
    }