.vcard {
  height: 205px;
  width: 155px;
  border-radius: 5px;
  border-radius: 5px;
  background-position: center;
  background-size: contain;
  transition: all 0.4s;
  position: relative;
  z-index: 997;
  transform: scale(1.08);
  cursor: pointer;
}
.vcard::after {
  content: "";
  height: 140px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0%;
  background: linear-gradient(to top, #131927 55%, transparent);
  display: none;
  border-radius: 0 0 5px 5px;
}
.vcard:hover {
  transform: scale(1.4);
  z-index: 998;
}
.vcard:hover .wrap {
  display: block;
}
.vcard:hover:after {
  display: block;
}
.vcard .wrap {
  margin-bottom: 4px;
  opacity: 0.8;
  display: none;
  position: absolute;
  bottom: 0;
  z-index: 99;
  left: 5px;
  padding: 2px;
}
.vcard .wrap h1 {
  font-size: 10px;
}
