

.banner{
    overflow: hidden;
    cursor: pointer;
    .bannerWrapper{
            display: flex !important;
            margin: auto;
            height: 410PX;
            border-radius: 5PX;
            overflow: hidden;
            box-shadow: 0px 0px 16px 8px #131927;
            .content{
                height: 100%;
                background-color: #030b17;
                line-height: 56px;
               .wrap{
                padding:  80px 12px 0 50px;

                position: relative;
                height: 100%;
                div:first-of-type{
                    font-size: 2rem;
                    font-weight: bolder;
                    color: white;

                }
                div:nth-child(2){
                    font-size: 1.1rem;
                    font-weight: 700;
                    color: rgba(256,256,256,0.7);
                    span:nth-child(2),span:first-of-type{
                        position: relative;
                        padding-right: 15px;
                        &::after{
                            content: "";
                            width: 3px;
                            height: 3px;
                            border-radius: 3px;
                            position: absolute;
                            top: 60%;
                            right: 5px;
                            background-color: rgba(256,256,256,0.7);
                        }
                    }
                   
                }
                div:last-of-type{
                    font-size: 1rem;
                    line-height: 20px;   
                    color: rgba(256,256,256,0.7);
                    width: 80%;

                }
                &::after{
                    content: "";
                        width: 100px;
                        height: 100%;
                        background:linear-gradient(to right,#030b17,transparent);
                        right: -100px;
                        position: absolute;
                        z-index: 2;
                        top: 0;
                }
               }
            }
        
    }
  
  .slick-list {
    margin: 0 -10px !important;
  }
  
  .slick-slide > div {
    padding: 0 10px !important;
  }


  .slick-prev{
    &::before{
        content: "\f053" !important;
    }
    left: -10px;
  }
  .slick-next{   
    &::before{
        content: "\f054" !important;
    }
    right: -10px;
}
.slick-prev,.slick-next{

    &::before{
        font-family: FontAwesome;
        font-size: 35px !important;
        display: none;

    }
    height: 100%;
    width: 60px;
    z-index: 99;
    // &.slick-disabled:before {
    //     opacity: 0 !important;
    // }
}
.slider{
    &:hover > .slick-prev:before,&:hover > .slick-next:before{
        display: block;
    }
}

}




